%heading {
  font-family: $font-display;
  font-weight: 400;
}

.heading-1 {
  @extend %heading;
  color: $color-grey-light-1;
  line-height: 1.5;
  font-size: 4.5rem;
  animation: slide-right 1.5s ease both;
  &-1 {
    font-size: 6.5rem;

    color: $color-primary;
  }
}

.heading-2 {
  @extend %heading;
  font-size: 4rem;
  font-style: italic;
  line-height: 1.5;
  &--light {
    color: $color-grey-light-1;
  }

  &--dark {
    color: $color-grey-dark-1;
  }
}

.heading-3 {
  @extend %heading;
  font-size: 1.6rem;
  color: $color-primary;
  text-transform: uppercase;
  animation: slide-right 1.5s ease both;
}

.heading-4 {
  @extend %heading;
  font-size: 1.9rem;

  &--light {
    color: $color-grey-light-1;
  }

  &--dark {
    color: $color-grey-dark-1;
  }
}
.heading-5 {
  @extend %heading;
  font-size: 2rem;
  color: $color-primary;
  text-transform: uppercase;
}

.btn {
  text-decoration: none;
  display: block;

  background-color: $color-primary;
  color: white;
  border: none;
  border-radius: 0;
  font-family: $font-display;
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 1rem;
  &:hover {
    background-color: $color-primary-dark;
    transform: translateY(-0.5rem);
  }
  &.disable {
    cursor: auto;
    background-color: #ccc;
    &:hover {
      transform: none;
    }
  }
}

.mb-sm {
  margin-bottom: 2rem;
}
.mb-md {
  margin-bottom: 3rem;
}
.mb-lg {
  margin-bottom: 4rem;
}
.mb-hg {
  margin-bottom: 8rem;
}
