.mobile-menu {
  position: fixed;
  right: 0px;
  top: 60px;
  width: 300px;

  &__logo {
    opacity: 0;
    &__img {
      width: 90%;
      height: 10rem;
      margin-left: 1rem;
    }
  }

  &__btn {
    background-color: unset;
    border: none;
    outline: none !important;
    cursor: pointer;

    & > span {
      background-color: rgb(255, 255, 255);
      width: 35px;
      height: 2px;
      display: block;
      margin-bottom: 9px;
      transition: transform 0.3s;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s;
    cursor: pointer;
    z-index: 200;
  }
  &__main {
    padding: 0;
    perspective: 2000px;
    transform-style: preserve-3d;
  }
  &__item {
    list-style: none;
    display: block;
    transform: translate3d(0, 0, -1000px);
    padding: 0 40px;
    transition: transform 0.3s, opacity 0.2s;
    opacity: 0;
  }
  &__link {
    display: block;
    margin-top: 30px;
    color: #333;
    text-decoration: none !important;
  }
}

.menu-open {
  &#global-container {
    transition: transform 0.5s;
    transform: translate(-300px, 60px);
    box-shadow: 0 8px 40px -10px rgba(0, 0, 0, 0.9);
  }

  & .mobile-menu__cover {
    opacity: 1;
    visibility: visible;
  }
  & .mobile-menu__btn {
    & > span {
      background-color: rgb(255, 255, 255);

      &:nth-child(1) {
        transition-delay: 70ms;
        transform: translateY(11px) rotate(135deg);
      }
      &:nth-child(2) {
        transition-delay: 0s;
        transform: translateX(-18px) scaleX(0);
      }
      &:nth-child(3) {
        transition-delay: 140ms;
        transform: translateY(-11px) rotate(-135deg);
      }
    }
  }
}
.open {
  & .mobile-menu__logo {
    opacity: 1;
  }

  & .mobile-menu__item {
    transform: none;
    opacity: 1;

    @for $i from 1 through 5 {
      &:nth-child(#{$i}) {
        transition-delay: $i * 0.07s;
      }
    }
  }
}

.main-title {
  font-weight: 600;
  font-size: 25px;
}
.sub-title {
  display: block;
  font-size: 14px;
}
